<style lang="less" scoped>
  .pass-tip {
    color: #666666;
    margin-bottom: 17px;
  }
</style>
<template>
  <div>
    <top-header title="修改账号密码"></top-header>
    <div class="page-container" style="margin-left: 100px;margin-top: 100px;">
      <el-form
        :model="ruleForm2"
        inline
        :show-message="false"
        status-icon
        label-position="top"
        :rules="rules2"
        ref="ruleForm2"
        label-width="100px"
        class="demo-ruleForm">
        <div>
          <el-form-item label="旧密码" prop="oldPass" size="small">
            <el-input type="password" v-model="ruleForm2.oldPass" auto-complete="off"></el-input>
          </el-form-item>
        </div>
        <div>
          <el-form-item label="新密码" prop="newPass" size="small">
            <el-input type="text" v-model="ruleForm2.newPass" auto-complete="off"></el-input>
          </el-form-item>
          <div class="pass-tip">8-20个字符，必须包含其中的三项：大写字母、小写字母、数字、英文特殊字符。</div>
        </div>
        <div>
          <el-form-item label="确认密码" prop="pass" size="small">
            <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
          </el-form-item>
        </div>
        <el-form-item style="margin-top: 40px">
          <el-button size="small" type="primary" @click="submitForm('ruleForm2')">确认修改</el-button>
          <el-button @click="resetForm('ruleForm2')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    let ch = /^(?![a-zA-Z]+$)(?![A-Z0-9]+$)(?![A-Z\\W()`~!@#$%^&*\-+=|\]{}[:;'<>,.? ]+$)(?![a-z0-9]+$)(?![a-z\\W()`~!@#$%^&*\-+=|\]{}[:;'<>,.? ]+$)(?![0-9\\W()`~!@#$%^&*\-+=|\]{}[:;'<>,.? ]+$)[a-zA-Z0-9\\W()`~!@#$%^&*\-+=|\]{}[:;'<>,.? ]{8,30}$/
    let checkoldPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入旧密码'))
      } else {
        if (this.ruleForm2.pass !== '') {
          this.$refs.ruleForm2.validateField('pass')
        }
        callback()
      }
    }
    let checknewPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入新密码'))
      } else {
        if (!ch.test(value)) {
          callback(new Error('密码不符合'))
        }
        console.log(ch.test(value))
        // if (this.ruleForm2.pass !== '') {
        //   this.$refs.ruleForm2.validateField('pass')
        // }
        callback()
      }
    }
    let checkPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm2.newPass) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      ruleForm2: {
        oldPass: '',
        newPass: '',
        pass: '',
        age: ''
      },
      rules2: {
        oldPass: [
          { validator: checkoldPass, trigger: 'blur' }
        ],
        newPass: [
          { validator: checknewPass, trigger: 'blur' }
        ],
        pass: [
          { validator: checkPass, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>



